/* General Styles */
body {
  font-family: "Arial", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  background-color: #f4f4f9;
  color: #333;
}
.park-theme {
  position: relative;
  width: 100%;
  height: 100vh; /* Full height */
  overflow: hidden; /* Ensures content doesn't spill outside */
}

/* Moving Background */
.background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the video covers the entire area */
  z-index: -1; /* Sends the video behind all other content */
}
.navbar {
  position: relative;
  z-index: 1; /* Ensures the navbar stays above the video */
}

@keyframes moveBackground {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -1000px 0;
  }
}

/* Hero Section */
.hero {
  text-align: center;
  padding: 200px 20px;
  background-color: transparent;
}

.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 40px;
}

.apple-button {
  width: 100px;
  height: auto;
  cursor: pointer;
}

/* Add spacing between sections */
section {
  margin-bottom: 200px;
}
